.main {
  &__container {
    display: flex;
    flex-wrap: wrap;
    width: $card-container-width;
    padding: 50px $main-container-paddings;
    margin: 0 auto;
  }

  &__card {
    &:not(:nth-last-child(-n + 4)) {
      margin-bottom: 46px;
    }

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }
  }
}
